.label-checkbox {
  display: inline-block;
  font-family: "open-sans-regular", Arial, sans-serif;
  font-size: 15px;
  cursor: pointer;
  position: relative;
  padding: 10px;
  padding-left: 40px;
}

.checkbox[type=checkbox] {
  display: none;
}

.checkbox + .input-circle {
  display: inline-block;
  position: relative;
  text-align: center;
  background-color: #FFF;
  border: 2px solid #ADB5BD;
  margin-right: 10px;
  width: 20px;
  height: 20px;
  box-sizing: border-box;
  border-radius: 1000px;
  vertical-align: bottom;
  transition: all 0.2s;
  position: absolute;
  left: 10px;
  top: 10px;
}

.label-checkbox:hover .input-circle {
  border: 2px solid #1380F6;
}

.checkbox:checked + .input-circle {
  background-color: #1380F6;
  border: 2px solid #1380F6;
  color: #fff;
}

.checkbox:checked + .input-circle:before {
  content: '\f00c';
  position: absolute;
  display: inline-block;
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  font-size: 10px;
  line-height: 1;
  left: 3px;
  top: 4px;
}

.label-checkbox--disabled {
  color: #737373;
  cursor: default;
  pointer-events: none;
}

.label-checkbox--disabled .checkbox + .input-circle {
  background-color: #E9ECEF;
  border: 2px solid #E9ECEF;
}

.label-radio {
  display: inline-block;
  font-family: "open-sans-regular", Arial, sans-serif;
  font-size: 15px;
  cursor: pointer;
}

.radio[type=radio] {
  display: none;
}

.radio + .input-circle {
  display: inline-block;
  position: relative;
  text-align: center;
  background-color: #FFF;
  border: 2px solid #ADB5BD;
  margin-right: 10px;
  width: 16px;
  height: 16px;
  box-sizing: border-box;
  border-radius: 1000px;
  vertical-align: sub;
  transition: all 0.2s;
}

.label-radio:hover .input-circle {
  border: 3px solid #fff;
  background-color: #DEE2E6;
  box-shadow: 0px 0px 0px 2px #1380F6;
}

.radio:checked + .input-circle {
  border: 3px solid #fff;
  background-color: #1380F6;
  box-shadow: 0px 0px 0px 2px #1380F6;
}

.label-radio--disabled {
  cursor: default;
  pointer-events: none;
}

.label-radio--disabled .radio + .input-circle {
  background-color: #DEE2E6;
  border: 4px solid #E9ECEF;
}

.label-text {
  display: inline-block;
  padding: 10px;
  padding-top: 30px;
  border-radius: 10px;
  position: relative;
  font-family: "open-sans-regular", Arial, sans-serif;
  width: 320px;
}

.input-text {
  font-size: 15px;
  font-family: "open-sans-regular", Arial, sans-serif;
  background: none;
  border: none;
  padding-bottom: 4px;
  outline: none;
  width: 100%;
  box-sizing: border-box;
  box-shadow: 0px 1px 0px #D9D9D9;
}

.input-text:focus {
  box-shadow: 0px 2px 0px #1380F6 !important;
}

.input-text:focus + .span-input-text {
  top: 9px;
  font-size: 13px;
  opacity: 0.5;
}

.input-text:not(:placeholder-shown) {
  box-shadow: 0px 1px 0px #D3D4D5;
}

.input-text:not(:placeholder-shown) + .span-input-text {
  top: 9px;
  font-size: 13px;
  opacity: 0.5;
}

.label-text:hover {
  background: #F8F9FA;
}

.label-text:hover .input-text {
  box-shadow: 0px 1px 0px #D3D4D5;
}

.span-input-text {
  font-size: 15px;
  font-weight: 400;
  position: absolute;
  top: 33px;
  left: 10px;
  transition: all .2s;
}

.input-text-error,
.input-text-success {
  display: none;
  width: 100%;
  padding-right: 20px;
  box-sizing: border-box;
  position: relative;
  font-size: 13px;
  font-family: "montserrat-medium", Arial, sans-serif;
}

.input-text-error {
  color: #FF6B6B;
}

.input-text-error:before {
  content: '\f12a';
  position: absolute;
  background: #FF6B6B;
  color: #fff;
  width: 16px;
  height: 16px;
  border-radius: 16px;
  top: 4px;
  right: 2px;
  display: inline-block;
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  font-size: 10px;
  line-height: 1.7;
  text-align: center;
}

.input-text-success {
  color: #40C057;
}

.input-text-success:before {
  content: '\f00c';
  position: absolute;
  background: #40C057;
  color: #fff;
  width: 16px;
  height: 16px;
  border-radius: 16px;
  top: 4px;
  right: 2px;
  display: inline-block;
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  font-size: 10px;
  line-height: 1.7;
  text-align: center;
}

.label-text--success .input-text {
  box-shadow: 0px 2px 0px #40C057;
}

.label-text--success .input-text-success {
  display: inline-block;
}

.label-text--error .input-text {
  box-shadow: 0px 2px 0px #FF6B6B;
}

.label-text--error .input-text-error {
  display: inline-block;
}

.btn,
.btn-small,
.btn-prew,
.btn-next,
.btn-numb {
  border: none;
  background: #E9ECEF;
  width: 200px;
  outline: none;
  font-family: "montserrat-semibold", Arial, sans-serif;
  font-size: 14px;
  color: #515254;
  padding: 12px 20px;
  box-sizing: border-box;
  border-radius: 200px;
  text-align: center;
  cursor: pointer;
  transition: all .2s;
  min-height: 42px;
}

.btn:hover,
.btn-small:hover,
.btn-prew:hover,
.btn-next:hover,
.btn-numb:hover {
  color: #000;
  background: #DEE2E6;
}

.btn:hover:after,
.btn-small:hover:after,
.btn-prew:hover:after,
.btn-next:hover:after,
.btn-numb:hover:after {
  color: #000;
}

.btn-arrow-r {
  text-align: left;
  position: relative;
  transition: all .2s;
}

.btn-arrow-r:after {
  content: "\f061";
  position: absolute;
  right: 20px;
  top: 14px;
  color: #515254;
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  margin-left: auto;
}

.btn--blue {
  background: #1380F6;
  color: #fff;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.2);
}

.btn--blue:hover {
  background: #439DFF;
  color: #fff;
}

.btn--blue:hover:after {
  color: #fff;
}

.btn--blue:after {
  color: #fff;
}

.btn-small {
  width: 150px;
  font-weight: 500;
  min-height: 36px;
  padding: 9px 20px;
}

.btn-small.btn-arrow-r:after {
  top: 11px;
}

.btn-prew,
.btn-next {
  width: 169px;
  font-weight: 500;
  position: relative;
  min-height: 36px;
  padding: 8px 20px;
}

.btn-numb {
  width: 36px;
  height: 36px;
  min-height: 36px;
  padding: 0;
  box-sizing: border-box;
}

.btn-numb.btn-numb--active {
  background: #1380F6;
  color: #fff;
  cursor: default;
}

.btn-prew {
  text-align: right;
}

.btn-prew:before {
  content: "\f060";
  position: absolute;
  left: 20px;
  top: 11px;
  color: #515254;
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  margin-left: auto;
}

.btn-next {
  text-align: left;
}

.btn-next:after {
  content: "\f061";
  position: absolute;
  right: 20px;
  top: 11px;
  color: #515254;
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  margin-left: auto;
}

.btn--red {
  background: #F03E3E;
  color: #fff;
}

.btn--red:hover {
  background: #ED5E5E;
  color: #fff;
}

.select2 {
  width: 300px;
  display: inline-block;
  font-size: 15px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}

.select2-results__option {
  font-size: 15px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  border-bottom: 1px solid #E9ECEF;
  margin: 0 10px;
}

.select2-name {
  display: block;
}

.select2-search--dropdown {
  display: none;
}

.select2-container--default .select2-selection--single {
  border: none;
  outline: none;
  border-bottom: 1px solid #BEBEBE;
}

.select2-container--default .select2-selection--single:hover {
  border-bottom: 1px solid #D3D4D5;
}

.select2-container--open .select2-selection--single {
  border-bottom: 2px solid #1380F6;
}

.select2-container--open .select2-selection--single:hover {
  border-bottom: 2px solid #1380F6;
}

.select2-selection__arrow:after {
  content: "\f107";
  position: absolute;
  right: 2px;
  top: -4px;
  color: #BEBEBE;
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  margin-left: auto;
  font-size: 23px;
}

.select2-selection__arrow [role=presentation] {
  display: none;
}

.select2-container--open .select2-selection__arrow:after {
  color: #1380F6;
}

.select2-container--open .select2-dropdown--below {
  border: none;
}

.select2-container--open .select2-results {
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
  border: none;
  border-radius: 8px;
  border: 1px solid #E9ECEF;
  overflow: hidden;
}

.select2-dropdown {
  border: none;
}

.select2-container--default .select2-results__option[aria-selected=true] {
  font-weight: 700;
  color: #000;
  background: none;
  position: relative;
  padding-right: 20px;
}

.select2-container--default .select2-results__option[aria-selected=true]:after {
  content: '\f00c';
  position: absolute;
  color: #1380F6;
  top: 8px;
  right: 5px;
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  font-size: 15px;
}

.select2-container--default .select2-results__option--highlighted[aria-selected] {
  font-weight: 700;
  color: #000;
  background: none;
}

.select2-container .select2-selection--single .select2-selection__rendered {
  padding-left: 0;
}

.label-select {
  padding: 10px;
  display: inline-block;
  border-radius: 10px;
}

.label-select:hover {
  background: #F8F9FA;
}

.select2-name {
  font-size: 13px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  color: #7F7F7F;
}

.select2-container--default .select2-selection--single {
